button {
   width: 75%;
   max-width: 350px;
   min-width: 250px;
   height: 40px;
   border: none;
   margin: 1rem 0;
   box-shadow: 0px 14px 9px -15px rgba(0, 0, 0, .25);
   border-radius: 8px;
   background-color: #546747;
   color: white;
   font-weight: 600;
   cursor: pointer;
}

h1 {
   width: 75%;
   max-width: 350px;
   min-width: 250px;
   font-size: 1.8rem;
   justify-content: center;
   align-items: center;
}

button:hover {
   background-color: #2B331F;
}

.input {
   display: flex;
   justify-content: center;
   align-items: center;

   width: 80%;
   max-width: 350px;
   min-width: 250px;
   height: 40px;
   border: none;
   margin: 0.5rem 0;
   background-color: #dad3d3;
   box-shadow: 0px 14px 9px -15px rgba(0, 0, 0, .25);
   border-radius: 8px;
   padding: 0 1rem;
   transition: all .2s ease-in;
}

.input:hover {
   -webkit-transform: translateY(-2px);
           transform: translateY(-2px);
}
.Navbar {
   height: 100vh;
   min-width: 262px;
   background-color: #2c3336;
   /* position: fixed;  */
   /* display: flex; */
 }
 
 .NavbarList {
   height: 100%;
   padding: 0;
   width: 100%;
 }
 
 .NavbarList .row {
   width: 100%;
   height: 60px;
   list-style-type: none;
   margin: 0%;
   display: flex;
   flex-direction: row;
   background-color:rgba(53, 65, 63, 0.867);
   color: white;
   justify-content: center;
   align-items: center;
   font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
 }
 
 .NavbarList .row:hover {
   cursor: pointer;
   background-color: #5f5f5f;
 }
 
 .NavbarList #active {
   background-color: #5f5f5f;
 }
 
 .row #icon {
   flex: 30% 1;
   display: grid;
   place-items: center;
 }
 
 .row #title {
   flex: 70% 1;
   place-items: center;
 }
.Player {
    background-color: aquamarine;
    border: 1px solid black;
    color:  blueviolet;
    font-size: 16px;
    max-width: 10px;
    float: right;
}

.Player-Div {
    margin: auto;
    position: relative;
    height: 20px;
}

.Song-Img {
    margin: auto;
}
.Page {
    background-color: rgb(83, 175, 129);
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    font-family: Georgia, 'Times New Roman', Times, serif;
    align-items: center;
}

.Visual-Img {
    margin: auto;
}
.profile-header {
    background-color: #ffffff;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.image-container {
    background-color: #bdbdbd;
    margin-top: 100px;
    height: 400px;
    width:  400px;
}

.text {
    color: white;
    justify-content: center;
}
.SearchPage {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  font-family: "Nunito", sans-serif;
  background-image: linear-gradient(90deg, #3a9c, #4f7a);
}
body {
  padding: 0;
  margin: 0;
}
.searchInputs {
    margin-top: 105px;
    display: flex;
}

.search input {
    background-color: white;
    border: 0;
    border-radius: 2px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    font-size: 18px;
    padding: 15px;
    height: 30px;
    width: 300px;
}

.searchIcon {
    height: 60px;
    width: 50px;
    background-color: white;
    display: grid;
    place-items: center;
}

input:focus {
    outline: none;
}

.searchIcon svg {
    font-size: 35px;
}

.dataResult {
    margin-top: 5px;
    width: 800px;
    height: 500px;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    overflow: hidden;
    overflow-y: auto;
}

.dataResult::-webkit-scrollbar {
    display: none;
}

.dataResult .dataItem {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    color: black;
}

.dataItem p {
    margin-left: 10px;
}

a {
    text-decoration: none;
}

a:hover {
    background-color: lightgrey;
}

#clearBtn {
    cursor: pointer;
}
